.img {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 90;
}

.container {
  position: relative;
  top: 0;
  left: 0;
  width: 40%;
  height: 100%;
  z-index: 89;
}

.btn {
  position: absolute;
  top: 45%;
  left: 44%;
  z-index: 100;
  width: 200px;
  height: 50px;
  border-radius: 25px;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  font-size: 25px;
  background-color: rgb(252, 255, 57);
  color: rgba(26, 26, 26, 0.808);
  cursor: pointer;
}

.btn:hover {
  background-color: rgb(251, 255, 22);
}

.title_container {
  position: absolute;
  display: flex;
  justify-content: center;
  width: 100%;
  top: 31%;
  z-index: 100;
}

.title {
  position: relative;
  z-index: 100;
  font-family: "Raleway", sans-serif;
  font-size: 450%;
  font-weight: bolder;
  color: rgb(253, 251, 251);
}

.text_container {
  position: absolute;
  top: 40%;
  z-index: 100;
  display: flex;
  justify-content: center;
  width: 100%;
}

.text {
  position: relative;
  z-index: 100;
  color: rgb(253, 251, 251);
  font-family: "Raleway", sans-serif;
  font-size: 150%;
}

.dark {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.466);
}
